body::-webkit-scrollbar {
    width: 0.7em;
}

body::-webkit-scrollbar-track {
    background-color: rgba(0, 0, 0, 0.8);
}

body::-webkit-scrollbar-thumb {
    background:#5e5e5e;
    border-radius:8px;
    box-shadow:inset 2px 2px 2px hsla(0,0%,100%,.25),inset -2px -2px 2px rgba(0,0,0,.25)
}